import {useContext, useState} from "react";
import {store} from "../../store/store";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Button,
  Dialog,
  DialogContent,
  Divider,
  Grid,
  Paper,
  Stack,
  styled,
  TextField,
  Typography
} from "@mui/material";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import MuiSwitch from "../MUISwitch";

const Label = styled(Typography)(() => ({
  fontSize: '14px',
  color: 'gray',
}));

const Item = styled(Grid)(() => ({
  marginTop: '8px',
  marginBottom: '8px',
}));

export default function Setting(props) {
  const [state, dispatch] = useContext(store);

  const [lightMode, setLightMode] = useState(false);
  const [panel, setPanel] = useState(false);
  const [open, setOpen] = useState(false);
  const [info, setInfo] = useState({
    name: state.user.name,
    email: state.user.email
  });
  const handleChangeDisplay = (e) => {
    setLightMode(e.target.checked);
    console.log(lightMode);
  };
  const handleChangePanel = (p) => (e, isExpanded) => {
    setPanel(isExpanded ? p : false);
  };
  const handleClickChangePW = () => {
    setOpen(true);
  };
  const handleCloseChangePW = () => {
    setOpen(false);
  };

  return (
    <>
      <Paper elevation={3} sx={{width: '600px', margin: '0 auto', marginTop: '50px', padding: '40px'}}>
        <Typography variant='h5' margin='20px 0'></Typography>
        <Divider sx={{marginTop: 3, marginBottom: 3}}/>
        <MuiSwitch name=' ' checked={lightMode} onClick={handleChangeDisplay} marginBottom={3}/>
        <Accordion expanded={panel === 'panel1'} onChange={handleChangePanel('panel1')}>
          <AccordionSummary expandIcon={<ExpandMoreIcon/>}>  </AccordionSummary>
          <AccordionDetails>
            <Grid container>
              <Item item xs={3}><Label></Label></Item>
              <Item item xs={9}>
                {info.name}
              </Item>
              <Item item xs={3}><Label>Email</Label></Item>
              <Item item xs={9}>
                {info.email}
              </Item>
              <Item item xs={3}><Label>PW</Label></Item>
              <Item item xs={9}>
                <Button size='small' onClick={handleClickChangePW}></Button>
              </Item>
              <Item item xs={3}><Label> </Label></Item>
              <Item item xs={9}>

              </Item>
              <Item item xs={3}><Label> </Label></Item>
              <Item item xs={9}>

              </Item>
              <Item item xs={3}><Label> </Label></Item>
              <Item item xs={9}>

              </Item>
            </Grid>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={panel === 'panel2'} onChange={handleChangePanel('panel2')}>
          <AccordionSummary expandIcon={<ExpandMoreIcon/>}>
            <Typography>  </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              Donec placerat, lectus sed mattis semper, neque lectus feugiat lectus,
              varius pulvinar diam eros in elit. Pellentesque convallis laoreet
              laoreet.
            </Typography>
          </AccordionDetails>
        </Accordion>
      </Paper>
      <Dialog open={open} onClose={handleCloseChangePW}>
        <DialogContent>
          <ChangePW></ChangePW>
          <Button color='error' onClick={handleCloseChangePW} fullWidth></Button>
        </DialogContent>
      </Dialog>
    </>
  );
}

function ChangePW() {

  return (
    <Stack spacing={2} sx={{marginBottom: '6px'}}>
      <Typography variant='h5' fontWeight='bold'> </Typography>

      <Stack spacing={1}>
        <TextField fullWidth label=' PW' type='password'/>
        <TextField fullWidth label=' PW' type='password'/>
        <TextField fullWidth label=' PW ' type='password'/>
        <Button type='submit' variant='contained' color='success'></Button>
      </Stack>
    </Stack>
  )
}
